<template>
  <Layout class="law-list">
    <Table highlight-row :columns="lawListColumns" :data="lawList"/>
    <Page
      style="margin-top: 0.5rem"
      :total="lawListPagination.total"
      :current="lawListPagination.page"
      :page-size="lawListPagination.pageSize"
      show-total
      show-sizer
      show-elevator
      @on-change="changeLawListPage"
      @on-page-size-change="changeLawListPageSize"
      />
  </Layout>
</template>

<script>
import { getLawList, deleteLaw } from '@/api/law'

export default {
  name: 'law-list',
  data () {
    return {
      lawListPagination: {
        total: 0,
        page: 1,
        pageSize: 10
      },
      lawList: [],
      lawListColumns: [
        {
          type: 'index',
          width: 60,
          align: 'center'
        },
        // {
        //   title: '类别',
        //   key: 'category'
        // },
        {
          title: '名称',
          render: (h, { column, row, index }) => {
            return h(
              'a',
              {
                props: {
                  href: 'javascript: void(0)'
                },
                on: {
                  click: () => {
                    this.$router.push({ name: 'LawItem', params: { lawId: row.id, parentId: -1 } })
                    this.$store.commit('law/navPush', { title: row.name, path: { name: 'LawItem', params: { lawId: row.id, parentId: -1 } } })
                    this.$store.commit('law/setLaw', row)
                  }
                }
              },
              row.name
            )
          }
        },
        {
          title: '操作',
          render: (h, { column, row, index }) => {
            return h(
              'span',
              [
                h(
                  'Button',
                  {
                    props: {
                      type: 'primary',
                      size: 'small'
                    },
                    nativeOn: {
                      click: () => {
                        this.$router.push({ name: 'LawDetail', params: { type: 'detail', id: row.id, category: row.categoryId } })
                      }
                    }
                  },
                  '编辑'
                ),
                h(
                  'Button',
                  {
                    props: {
                      type: 'error',
                      size: 'small'
                    },
                    style: {
                      marginLeft: '0.5rem'
                    },
                    nativeOn: {
                      click: () => {
                        this.deleteLaw(row, index)
                      }
                    }
                  },
                  '删除'
                )
              ]
            )
          }
        }
      ]
    }
  },
  created () {
    this.getLawList(this.$route.params.categoryId)
  },
  watch: {
    '$route.params.categoryId': {
      handler () {
        this.getLawList(this.$route.params.categoryId)
      },
      deep: true
    }
  },
  methods: {
    changeLawListPage (page) {
      this.lawListPagination.page = page
      this.getLawList()
    },
    changeLawListPageSize (pageSize) {
      this.lawListPagination.pageSize = pageSize
      this.getLawList()
    },
    getLawList (categoryId) { // 获取法律列表
      getLawList({
        categoryId: categoryId,
        pageNo: this.lawListPagination.page,
        pageSize: this.lawListPagination.pageSize
      }).then(({ data, response }) => {
        if (response.success) {
          this.lawList = data ? data.recordList : []
          this.lawListPagination.total = data ? data.totalCount : 0
        } else {
          this.$Message.error(response.msg)
        }
      })
    },
    deleteLaw (row, index) {
      this.$Modal.confirm({
        title: '提示',
        content: '删除后将无法恢复，请确定是否删除！',
        onCancel: () => {},
        onOk: () => {
          deleteLaw({
            id: row.id
          }).then(({ data, response }) => {
            if (response.success) {
              this.$Message.success('删除成功！')
              this.lawList.splice(index, 1)
            } else {
              this.$Message.error(response.msg)
            }
          })
        }
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
.law-list
  background none
</style>
